* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    overflow: hidden;
    font-family: 'Nunito', sans-serif;
}

body::-webkit-scrollbar{
    display: none;
}

html, body {
    padding: 0;
    margin: 0;
    height: 100%;
    background-color: #97F0FF;
}

a {
    color: #000;
}

.mainHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #97F0FF;
    font-size: 3rem;
    position: relative;
    z-index: 10;
    height: 3vh;
    text-align: center;
}

.columns {
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    height: 84vh;
    position: relative;
    width: 100%;
    top: 0;
    bottom: 0;
}

.column {
    overflow: auto;
    height: auto;
    padding: .5rem;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    width: 33.33vw;
}

.column::-webkit-scrollbar {
    display: none;
}

.feedName {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    background-color: #97F0FF;
}

@media only screen and (max-width: 768px) {
    .columns {
        display: -ms-flexbox;
        display: flex;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        -webkit-scroll-snap-points-x: repeat(300px);
            -ms-scroll-snap-points-x: repeat(300px);
                scroll-snap-points-x: repeat(300px);
        -webkit-scroll-snap-type: mandatory;
            -ms-scroll-snap-type: mandatory;
                scroll-snap-type: mandatory;
    }
    .column {
        -ms-flex-negative: 0;
            flex-shrink: 0;
        width: 100vw;
    }
}


